import React, { Component } from 'react';
import myImg from '../img/New folder/myImgCut.png';
class Img extends Component {
    state = {  }
    render() { 
        return ( 
            <div className="img">
                <div className="photo">
                    <img src={myImg} alt="" width="100%" height="100%" />
                    <div className="blur-img"></div>
                </div>
            </div>
        );
    }
}

export default Img;