import React, { Component } from 'react';


import Info from './Tabs/Info';
import Skilles from './Tabs/Skilles';
import Contact from './Tabs/Contact';
import Exp from './Tabs/Exp';
import Home from './Tabs/Home';

import { BrowserRouter, Link, Route, Switch } from 'react-router-dom';

import Img from './Img';
import Corner from './Corner';

class Main extends Component {
    state = {  }
    componentDidMount = () => {
        document.querySelector(".main").style.height = window.innerHeight + "px";
        document.querySelector(".bal-Home").style.transform = "translateX(25px)";
    }
    move = (item) => {
        switch (item) {
            case 'Home':
                document.querySelector(".bal-Home").style.transform = "translateX(25px)";
                document.querySelector(".bal-Skilles").style.transform = "translateX(-25px)";
                document.querySelector(".bal-Info").style.transform = "translateX(-25px)";
                document.querySelector(".bal-Exp").style.transform = "translateX(-25px)";
                document.querySelector(".bal-Contact").style.transform = "translateX(-25px)";
                document.querySelector(".name").style.transform = "translateY(-40px)";
                
                
                break;

                case 'Info':
                    document.querySelector(".bal-Home").style.transform = "translateX(-25px)";
                    document.querySelector(".bal-Skilles").style.transform = "translateX(-25px)";
                    document.querySelector(".bal-Info").style.transform = "translateX(25px)";
                    document.querySelector(".bal-Exp").style.transform = "translateX(-25px)";
                    document.querySelector(".bal-Contact").style.transform = "translateX(-25px)";
                    document.querySelector(".name").style.transform = "translateY(30px)";
                    break;
                case 'skilles':
                document.querySelector(".bal-Home").style.transform = "translateX(-25px)";
                document.querySelector(".bal-Skilles").style.transform = "translateX(25px)";
                document.querySelector(".bal-Info").style.transform = "translateX(-25px)";
                document.querySelector(".bal-Exp").style.transform = "translateX(-25px)";
                document.querySelector(".bal-Contact").style.transform = "translateX(-25px)";
                document.querySelector(".name").style.transform = "translateY(30px)";

                // document.querySelector(".img").style.transform = "translateX(500px)";
                // document.querySelector(".tabs").style.width = "200%";
                // document.querySelector(".img").style.width = "50%";
                
                break;

            case 'Exp':
                document.querySelector(".bal-Home").style.transform = "translateX(-25px)";
                document.querySelector(".bal-Skilles").style.transform = "translateX(-25px)";
                document.querySelector(".bal-Info").style.transform = "translateX(-25px)";
                document.querySelector(".bal-Exp").style.transform = "translateX(25px)";
                document.querySelector(".bal-Contact").style.transform = "translateX(-25px)";
                document.querySelector(".name").style.transform = "translateY(30px)";
                break;

            case 'Contact':
                document.querySelector(".bal-Home").style.transform = "translateX(-25px)";
                document.querySelector(".bal-Skilles").style.transform = "translateX(-25px)";
                document.querySelector(".bal-Info").style.transform = "translateX(-25px)";
                document.querySelector(".bal-Exp").style.transform = "translateX(-25px)";
                document.querySelector(".bal-Contact").style.transform = "translateX(25px)";
                document.querySelector(".name").style.transform = "translateY(30px)";
                break;
        
            default:
                break;
        }
    }
    render() { 
        return ( 
            <div className="main">
                <BrowserRouter>
                <div className="white"></div>
                <div className="select"></div>
                    <div className="nav">
                        <Link onClick={(() => {this.move("Home")})} className="link" to="/Home"><div className="bal bal-Home"></div>Home</Link>
                        <Link onClick={(() => {this.move("skilles")})} className="link" to="/Skilles"><div className="bal bal-Skilles"></div>Skilles</Link>
                        <Link onClick={(() => {this.move("Info")})} className="link" to="/Info"><div className="bal bal-Info"></div>Info</Link>
                        <Link onClick={(() => {this.move("Exp")})} className="link" to="/Exp"><div className="bal bal-Exp"></div>Experience</Link>
                        <Link onClick={(() => {this.move("Contact")})} className="link" to="/Contact"><div className="bal bal-Contact"></div>Contact</Link>
                    </div>
                    <div className="tabs">
                        <div className="name">
                            <p>Abd Alrahman Daas</p>
                        </div>
                        <Switch>
                            <Route path="/" component={Home} exact/>
                            <Route path="/Home" component={Home} exact/>
                            <Route path="/Info" component={Info} exact/>
                            <Route path="/Skilles" component={Skilles} exact/>
                            <Route path="/Contact" component={Contact} exact/>
                            <Route path="/Exp" component={Exp} exact/>
                        </Switch>
                    </div>
                </BrowserRouter>
                <Img />
                <Corner />
            </div>
        );
    }
}
export default Main;