import React, { Component } from 'react';
import './skilles.css';

//png
import html from './png/html5.png';
import css3 from './png/css3.png';
import js from './png/js.png';
import reactjs from './png/react.png';


class Skilles extends Component {
    state = {  }
    render() { 
        return ( 
            <div className="Skilles">
                <Item value="90%" id="pro1" img={html} />
                <Item value="85%" id="pro2" img={css3} />
                <Item value="75%" id="pro3" img={js} />
                <Item value="90%" id="pro4" img={reactjs} />
                
            </div>
        );
    }
}


class Item extends Component {
    state = {  }
    componentDidMount = () => {
        document.getElementById(this.props.id).style.width = this.props.value;
    }
    render() { 
        return ( 
            <div className="item">
                <div className="logo">
                    <img src={this.props.img} alt="" width="100%" height="100%" />
                </div>
                <div className="full-progress"><div className="progress" id={this.props.id}></div></div>
                <p className="value">{this.props.value}</p>
            </div>
        );
    }
}


export default Skilles;